export default {
  props: ["isPresentationsInFullScreen", "curSlideOverride"],
  mounted() {
    const slides = document.querySelectorAll(".slide");
    this.totalSlides = slides.length;
    this.addKeyboardEventsToToggleSlides();
  },
  unmounted() {
    window.removeEventListener("keyup");
  },
  data() {
    return {
      curSlide: 0,
      totalSlides: undefined,
    };
  },
  methods: {
    nextSlide: function () {
      this.$parent.updateCurSlideOverride(null);
      if (this.curSlide === this.totalSlides - 1) {
        this.curSlide = 0;
      } else {
        this.curSlide++;
      }
    },
    prevSlide: function () {
      this.$parent.updateCurSlideOverride(null);
      if (this.curSlide === 0) {
        this.curSlide = this.totalSlides - 1;
      } else {
        this.curSlide--;
      }
    },
    addKeyboardEventsToToggleSlides: function () {
      window.addEventListener("keyup", (e) => {
        if (e.key === "ArrowRight") {
          this.nextSlide();
        } else if (e.key === "ArrowLeft") {
          this.prevSlide();
        } else if (e.key === "Escape") {
          this.togglePresentationInFullScreen();
        }
      });
    },
    updateCurSlide: function (slideNumber) {
      this.$parent.updateCurSlideOverride(null);
      this.curSlide = slideNumber;
    },
  },
};
